.dropdown-wrapper.error .dropdown-button {
  border-color: #dc2626;
}
.dropdown-wrapper.error .dropdown-button:focus {
  border-color: #dc2626;
  box-shadow: 0 0 0 3px rgba(220, 38, 38, 0.1);
}

.dropdown-label {
  display: block;
  margin-bottom: 8px;
  font-weight: 500;
  color: #374151;
  font-size: 14px;
}

.required {
  color: #dc2626;
  margin-left: 4px;
}

.generic-dropdown {
  position: relative;
  width: 100%;
}

.dropdown-button {
  width: 100%;
  padding: 12px 16px;
  border: 2px solid #d1d5db;
  border-radius: 8px;
  background: white;
  cursor: pointer;
  transition: all 0.2s;
  text-align: left;
  font-size: 14px;
  min-height: 48px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.dropdown-button:hover:not(:disabled) {
  border-color: #9ca3af;
}
.dropdown-button:focus {
  outline: none;
  border-color: #3b82f6;
  box-shadow: 0 0 0 3px rgba(59, 130, 246, 0.1);
}
.dropdown-button.active {
  border-color: #3b82f6;
  box-shadow: 0 0 0 3px rgba(59, 130, 246, 0.1);
}
.dropdown-button:disabled {
  background-color: #f9fafb;
  color: #9ca3af;
  cursor: not-allowed;
  border-color: #e5e7eb;
}
.dropdown-button.error {
  border-color: #dc2626;
}
.dropdown-button.error:focus {
  border-color: #dc2626;
  box-shadow: 0 0 0 3px rgba(220, 38, 38, 0.1);
}

.dropdown-content {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
}

.placeholder {
  color: #9ca3af;
  font-style: italic;
}

.selected-item {
  display: flex;
  align-items: center;
  gap: 8px;
  width: 100%;
}

.item-icon {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 20px;
  height: 20px;
  flex-shrink: 0;
}

.icon {
  width: 16px;
  height: 16px;
  color: #6b7280;
}

.selected-text {
  color: #111827;
  font-weight: 500;
  flex: 1;
}

.dropdown-actions {
  display: flex;
  align-items: center;
  gap: 8px;
  flex-shrink: 0;
}

.clear-btn {
  background: none;
  border: none;
  padding: 4px;
  border-radius: 4px;
  cursor: pointer;
  transition: all 0.2s;
  display: flex;
  align-items: center;
  justify-content: center;
}
.clear-btn:hover {
  background-color: #f3f4f6;
}

.clear-icon {
  width: 16px;
  height: 16px;
  color: #6b7280;
}

.dropdown-arrow {
  width: 20px;
  height: 20px;
  color: #6b7280;
  transition: transform 0.2s;
  flex-shrink: 0;
}
.dropdown-arrow.rotated {
  transform: rotate(180deg);
}

.dropdown-menu {
  position: absolute;
  top: 100%;
  left: 0;
  right: 0;
  background: white;
  border: 2px solid #d1d5db;
  border-top: none;
  border-radius: 0 0 8px 8px;
  box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.1);
  z-index: 10;
  max-height: 300px;
  overflow-y: auto;
}

.dropdown-header {
  padding: 12px 16px;
  background-color: #f9fafb;
  border-bottom: 1px solid #e5e7eb;
  font-weight: 500;
  color: #374151;
  font-size: 14px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.options-count {
  font-size: 12px;
  color: #6b7280;
  font-weight: normal;
}

.search-container {
  padding: 12px 16px;
  border-bottom: 1px solid #e5e7eb;
}

.search-input {
  width: 100%;
  padding: 8px 12px;
  border: 1px solid #d1d5db;
  border-radius: 6px;
  font-size: 14px;
  transition: border-color 0.2s;
}
.search-input:focus {
  outline: none;
  border-color: #3b82f6;
  box-shadow: 0 0 0 2px rgba(59, 130, 246, 0.1);
}
.search-input::placeholder {
  color: #9ca3af;
}

.no-results {
  padding: 16px;
  text-align: center;
  color: #6b7280;
  font-style: italic;
  font-size: 14px;
}

.dropdown-item {
  width: 100%;
  padding: 12px 16px;
  border: none;
  background: none;
  text-align: left;
  cursor: pointer;
  transition: background-color 0.2s;
  border-bottom: 1px solid #f3f4f6;
}
.dropdown-item:hover:not(.disabled) {
  background-color: #f3f4f6;
}
.dropdown-item.selected {
  background-color: #eff6ff;
  color: #1d4ed8;
}
.dropdown-item.disabled {
  color: #9ca3af;
  cursor: not-allowed;
  background-color: #f9fafb;
}
.dropdown-item:last-child {
  border-bottom: none;
}

.item-content {
  display: flex;
  align-items: center;
  gap: 12px;
}

.item-details {
  flex: 1;
}

.item-name {
  font-weight: 500;
  color: #111827;
  margin-bottom: 2px;
}

.item-description {
  font-size: 12px;
  color: #6b7280;
  line-height: 1.4;
}

.field-error {
  margin-top: 8px;
  color: #dc2626;
  font-size: 12px;
  font-weight: 500;
}
